.card {
  background: #fff;
  border-radius: 2px;
  height: 150px;
  margin: 1rem;
  position: relative;
  width: 150px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  overflow: hidden;
}

.card>.img-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.card>.img-container>img {
  width: 100%;
}



